import React from 'react';
import { useParams } from 'react-router-dom';
import {dress, jacket, coat, shirt, jeans, trouser, jogger, pullover} from './shop-data';
import Category from './Category';

function SelectCategory() {
  const {id} = useParams();
  const kind = [dress, jacket, coat, shirt, jeans, trouser, jogger, pullover];

  switch(id) {
    //   
    case 'dress':
      return(<Category data={dress} id={''} />);
    case 'jacket':
      return(<Category data={jacket} id={''} />);
    case 'coat':
      return(<Category data={coat} id={''} />);
    case 'shirt':
      return(<Category data={shirt} id={''} />);
    case 'jeans':
      return(<Category data={jeans} id={''} />);
    case 'trouser':
      return(<Category data={trouser} id={''} />);
    case 'jogger':
      return(<Category data={jogger} id={''} />);
    case 'pullover':
      return(<Category data={pullover} id={''} />);
    //  
    case 'date':
    case 'interview':
    case 'home':
    case 'work':
    case 'campus':
    case 'guest':
      return(<ThemeCategory id={id} kind={kind} />);
    // MBTI 
    default :
      return(<MbtiCategory id={id} kind={kind} />);
  }
}

function MbtiCategory({id, kind}) {
  let list = [];
  let data;

  kind.forEach(k => {
    list.push(k.filter(k => k.mbti.indexOf(id) > -1));
  });

  data = inputData(data, list, id);
  
  return(<Category data={data} id={id} />);
}

function ThemeCategory({id, kind}) {
  let list = [];
  let data;

  switch(id) {
    case 'date' : id = ''; break;
    case 'interview': id = ''; break;
    case 'home': id = ''; break;
    case 'work': id = ''; break;
    case 'campus': id = ''; break;
    case 'guest': id = ''; break;
  }

  kind.forEach(k => {
    list.push(k.filter(k => k.theme.indexOf(id) > -1));
  });

  data = inputData(data, list, id);

  return(<Category data={data} id={id} />);
}

function inputData(data, list) {
  data = list.reduce((acc, cur) => { return acc.concat(cur)});
  return data;
}

export default SelectCategory;