import React, { useState, useMemo } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import cn from "classnames";
import style from "./Home.module.css";
import {
  dress,
  jacket,
  coat,
  shirt,
  jeans,
  trouser,
  jogger,
  pullover,
} from "./shop-data";
import Sidebar from "./Sidebar";

function Home() {
  const userMbti = useSelector((state) => state.login.mbti);
  let url = `/shop/${userMbti}`;

  return (
    <section className={cn(style.container)}>
      <aside>
        <Sidebar />
      </aside>
      <Slide />

      <Link to="/search" className={cn(style.searchBox)}>
        <h3> .</h3>
        <button type="button" className={cn(style.searchBtn)}>
          <img
            src="/shop/public-assets/icons/searchBtn.png"
            alt="The search button"
          />
        </button>
      </Link>

      <article className={cn(style.recommend)}>
        <h1>
          <span className={cn(style.point)}>  </span>
        </h1>
        <div className={cn(style.grid)}>
          <UserRecommend mbti={userMbti} />
        </div>
        <Link to={url} className={cn(style.moreBtn)}>
            
        </Link>
      </article>

      <aside className={cn(style.banner)}>
        <img src="/public-assets/event/banner1.png" alt="The banner1 image" />
        <img src="/public-assets/event/banner2.png" alt="The banner2 image" />
      </aside>

      <article className={cn(style.recommend)}>
        <h1>
           <span className={cn(style.point)}> </span>
        </h1>
        <div className={cn(style.header)}>
          <p className={cn(style.themeInfo)}>
            <strong className={cn(style.title)}>  </strong>
                 ,    
             !
            <br />
            <br />
                      
            .
            <br />
                     !  
              ~
          </p>
          <div className={cn(style.imgContainer)}>
            <img src="/public-assets/theme/cam.png" alt="Recommend campus" />
          </div>
        </div>

        <div className={cn(style.grid)}>
          <ThemeRecommend
            kind1={dress}
            kind2={jacket}
            kind3={jogger}
            kind4={pullover}
            title={""}
          />
        </div>
        <Link to="/shop/campus" className={cn(style.moreBtn)}>
            
        </Link>

        <div className={cn(style.header)}>
          <div className={cn(style.imgContainer)}>
            <img src="/public-assets/theme/date.png" alt="Recommend Date" />
          </div>
          <p className={cn(style.themeInfo)}>
            <strong className={cn(style.title)}>  </strong>
                    !
            <br />
            <br />
                    .
            <br />
                     
            
          </p>
        </div>

        <div className={cn(style.grid)}>
          <ThemeRecommend
            kind1={dress}
            kind2={shirt}
            kind3={trouser}
            kind4={pullover}
            title={""}
          />
        </div>
        <Link to="/shop/date" className={cn(style.moreBtn)}>
            
        </Link>

        <div className={cn(style.header)}>
          <p className={cn(style.themeInfo)}>
            <strong className={cn(style.title)}>  </strong>
                  !
            <br />
            <br />
                  !
            <br />
                 .
            <br />
                !  ~
          </p>
          <div className={cn(style.imgContainer)}>
            <img src="/public-assets/theme/guset.png" alt="Recommend guest" />
          </div>
        </div>

        <div className={cn(style.grid)}>
          <ThemeRecommend
            kind1={dress}
            kind2={coat}
            kind3={jacket}
            kind4={shirt}
            title={""}
          />
        </div>
        <Link to="/shop/guest" className={cn(style.moreBtn)}>
            
        </Link>
      </article>
    </section>
  );
}

function Slide() {
  let [counter, setCounter] = useState(0);

  const clickPrevBtn = () => {
    setCounter(counter - 1);
    carousel();
  };

  const clickNextBtn = () => {
    setCounter(counter + 1);
    carousel();
  };

  const carousel = () => {
    if (counter < 0) {
      setCounter(1);
    } else if (counter == 1) {
      setCounter(0);
    }
  };

  return (
    <article className={cn(style.slideContainer)}>
      <button
        type="button"
        className={cn(style.slideBtn, style.prevBtn)}
        onClick={clickPrevBtn}
      >
        <img src="/public-assets/icons/prevBtn.png" alt="The previous button" />
      </button>

      <button
        type="button"
        className={cn(style.slideBtn, style.nextBtn)}
        onClick={clickNextBtn}
      >
        <img src="/public-assets/icons/nextBtn.png" alt="The next button" />
      </button>

      <div
        className={cn(style.slideImgContainer)}
        style={{
          left: "0%",
          transform: `translateX(-${counter * 100}%)`,
        }}
      >
        <img src="/public-assets/event/slide1.png" alt="The event1 image" />
      </div>

      <div
        className={cn(style.slideImgContainer)}
        style={{
          left: "100%",
          transform: `translateX(-${counter * 100}%)`,
        }}
      >
        <img
          src="/public-assets/event/slide2.png"
          alt="The event2 image"
          className={cn(style.event2)}
        />
      </div>
    </article>
  );
}

function UserRecommend({ mbti }) {
  let list = [];

  useMemo(() => {
    list.push(dress.find((d) => d.mbti.indexOf(mbti) > -1));
    list.push(jacket.find((j) => j.mbti.indexOf(mbti) > -1));
    list.push(coat.find((c) => c.mbti.indexOf(mbti) > -1));
    list.push(shirt.find((d) => d.mbti.indexOf(mbti) > -1));
    list.push(jeans.find((b) => b.mbti.indexOf(mbti) > -1));
    list.push(trouser.find((t) => t.mbti.indexOf(mbti) > -1));
    list.push(jogger.find((g) => g.mbti.indexOf(mbti) > -1));
    list.push(pullover.find((p) => p.mbti.indexOf(mbti) > -1));
  }, [mbti]);

  return list.map((item) => {
    if (item === undefined) {
      return null;
    }
    return (
      <Link to={item.url} className={cn(style.cardContainer)} key={item.id}>
        <div className={cn(style.imgContainer)}>
          <img src={item.mainImg[0].img} alt={item.title} />
        </div>
        <div className={cn(style.cardInfo)}>
          <span className={cn(style.kindInfo)}>{item.theme[1]}</span>
          <span>{item.title}</span>
          <h4>{item.cost.toLocaleString("ko-KR")}</h4>
        </div>
      </Link>
    );
  });
}

function ThemeRecommend({ kind1, kind2, kind3, kind4, title }) {
  let list = [];
  list.push(kind1.find((k) => k.theme.indexOf(title) > -1));
  list.push(kind2.find((k) => k.theme.indexOf(title) > -1));
  list.push(kind3.find((k) => k.theme.indexOf(title) > -1));
  list.push(kind4.find((k) => k.theme.indexOf(title) > -1));

  return list.map((item) => {
    return (
      <Link to={item.url} className={cn(style.cardContainer)} key={item.id}>
        <div className={cn(style.imgContainer)}>
          <img src={item.mainImg[0].img} alt={item.title} />
        </div>
        <div className={cn(style.cardInfo)}>
          <span className={cn(style.kindInfo)}>{title}</span>
          <span>{item.title}</span>
          <h4>{item.cost.toLocaleString("ko-KR")}</h4>
        </div>
      </Link>
    );
  });
}

export default Home;
